.App {
  /*text-align: center;*/
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 8s linear;
          animation: App-logo-spin infinite 8s linear;

  -webkit-transform-origin: top;

      -ms-transform-origin: top;

          transform-origin: top;
  height: 150px;
}

.App-header {
  background-color: #222;
  height: 290px;
  padding: 20px;
  padding-bottom: 100px;
  margin-bottom: 10px;
  color: white;
  text-align: center;
  margin-right: -1rem;
}

.App-intro {
  font-size: large;
  position: relative;
}
@media only screen and (max-width:600px){
  p#dataupated{
   display: none !important;
  }
}

@-webkit-keyframes App-logo-spin {
  0% { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
  50% { -webkit-transform: rotate(12deg); transform: rotate(12deg); }
  100% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
}

@keyframes App-logo-spin {
  0% { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
  50% { -webkit-transform: rotate(12deg); transform: rotate(12deg); }
  100% {-webkit-transform: rotate(-10deg);transform: rotate(-10deg);}
}

@-webkit-keyframes fade-in {
  0%{opacity:0}
  100% {opacity: 1}
}

@keyframes fade-in {
  0%{opacity:0}
  100% {opacity: 1}
}



